import React, { Component } from 'react'
import './index.less'
import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread';
import { Input, Form, Row, Col, Radio, Table, Button } from 'antd';
import AddModal from "./addModal";
import api from '../../../api';

const FormItem = Form.Item

class addPaper extends Component {
    constructor(props) {
        super(props)
        this.state = {
            type: 0, // 0 1
            addModalShow: false, // 
            questionsType: [],
            paperDetail: {
                checked: true
            },
            breadcrumbData: [
                { name: '' },
                { name: '' },
                { name: '' }
            ],
            dataSource: [{
                name: '',
                type: '',
                user: '',
                time: '2019-12-10'
            },{
                name: '',
                type: '',
                user: '',
                time: '2019-12-10'
            }]
        }
        this.columns=[{
            title: '',
            dataIndex: 'name'
        },{
            title: '',
            dataIndex: 'type'
        },{
            title: '',
            dataIndex: 'user'
        },{
            title: '',
            dataIndex: 'time'
        }]
    }
    componentDidMount() {
        this.listTypeDetails()
    }
    // 
    handleChangeRadio = () => {
        const { paperDetail } = this.state;
        this.setState({
            paperDetail: { ...paperDetail, checked: !paperDetail.checked}   
        })
    }
    listTypeDetails = () => {
        Api.listTypeDetails({
            itemsId: 5
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    questionsType: res.data.list
                })
            }
        })
    }
    // 
    handleAddBtn = () => {
        const arr = this.state.questionsType
        // if (!this.state.paperDetail.checked) {
        //     arr.push({code: 4,name: ''},{code: 5,name: ''});
        // }
        this.setState({
            type: 0,
            addModalShow: true,
            questionsType: arr
        })
    }
    // 
    handleCancel = () => {
        this.setState({
            addModalShow: false
        })
    }
    // 
    handleOk = () => {
        this.setState({
            addModalShow: false
        })
    }
    render() {
        const { breadcrumbData, dataSource, type, addModalShow, questionsType, paperDetail} = this.state;
        const { getFieldDecorator } = this.props.form;
        return (
            <div className="addPaper">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="conster-wrap">
                        <p className="title"></p>
                        <Form labelCol={{ span: 5 }} wrapperCol={{ span: 16 }}>
                            <Row gutter={24}>
                                <Col span={12}>
                                    <FormItem label="">
                                        {getFieldDecorator('examName',{
                                            initialValue: paperDetail.examDuration,
                                            rules: [{ required: true, message: '' }],
                                        })(
                                            <Input maxLength={60} placeholder="" autoComplete='off' />
                                        )}
                                    </FormItem>
                                </Col>
                                <Col span={12}>
                                    <FormItem label="">
                                        {getFieldDecorator('isPeixun',{
                                            initialValue: paperDetail.isPeixun,
                                        })(
                                            <Radio checked={paperDetail.isPeixun} onClick={this.handleChangeRadio}></Radio>
                                        )}
                                    </FormItem>
                                </Col>
                            </Row>
                            <Row gutter={24}>
                                <Col span={12}>
                                    <FormItem label="">
                                        {getFieldDecorator('examDuration', {
                                            initialValue: paperDetail.examDuration,
                                            rules: [
                                                { required: true, message: '' },
                                                { pattern:/^([1-9][0-9]*)$/, message: '!' }
                                            ]
                                        })(
                                            <Input placeholder="" autoComplete='off' suffix="" />
                                        )}
                                    </FormItem>
                                </Col>
                                <Col span={12}>
                                    <FormItem label="">
                                        0
                                    </FormItem>
                                </Col>
                            </Row>
                        </Form>
                    </div>
                    <div className="dashed"></div>
                    <div className="conster-wrap">
                        <p className="title lineHeight"></p>
                        <div className="table-wrap">
                            <div className="table-title">
                                <div className="left-title">
                                    <span className="testType"></span>
                                    <span>2</span>
                                    <span>2</span>
                                    <span>2</span>
                                </div>
                                <div className="right-btn">
                                    <span></span>
                                    <span></span>
                                </div>
                            </div>
                            <Table
                                className="tableClass"
                                dataSource={dataSource}
                                columns={this.columns}
                                pagination={false}
                            />
                        </div>
                        {/* <div className="table-wrap">
                            <div className="table-title">
                                <div className="left-title">
                                    <span className="testType"></span>
                                    <span>2</span>
                                    <span>2</span>
                                    <span>2</span>
                                </div>
                                <div className="right-btn">
                                    <span></span>
                                    <span></span>
                                </div>
                            </div>
                            <Table
                                className="tableClass"
                                dataSource={dataSource}
                                columns={this.columns}
                                pagination={false}
                            />
                        </div> */}
                        <div className="table-wrap-blue" onClick={this.handleAddBtn}></div>
                    </div>
                </div>
                <div className="bottom-btn">
                    <Button type="primary"> </Button>
                    <Button> </Button>
                </div>
                <AddModal
                    type={type}
                    questionsType={questionsType}
                    addModalShow={addModalShow}
                    handleOk={this.handleOk}
                    handleCancel={this.handleCancel}
                />
            </div>
        )
    }
}
export default Form.create()(addPaper)