import React, { Component } from 'react'
import './index.less'
// import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread';
import SearchForm from "./search-form"; // 
import Table from "./table-list"; // 
class systemLog extends Component {
    constructor(props) {
        super(props)
        this.state = {
            breadcrumbData: [
              { name: '' },
              { name: '' }
            ],
            dataList: [],     // 
            loading: false,   // 
        }
    }
    componentDidMount() {
      this.queryTableList();
    }
    // 
    handleSearchBtn = (value) =>{
      console.log(value,'----search---');
    }
    // 
    queryTableList = () =>{
      this.setState({
        loading: true
      },() => {
        setTimeout(() => {
          const json = {
            userName: 'admin',
            name: 'ayuan',
            dept: 'IT',
            role: '',
            phone: '18437980170',
            email: '1572720803@qq.com',
            time: '2019-08-21',
            cishu: '100',
            status: '1'
          }
          this.setState({
            loading: false,
            dataList: Array(10).fill(json)
          })
        }, 1000);
      })
    }
    // 
    handleChangePage = (pageNo,pageSize) => {
      this.queryTableList();
      console.log(pageNo, pageSize,'----pageNo, pageSize---')
    }
    // 
    handleDelete = (code) => {
      console.log(code, '--------')
    }
    render() {
        const { breadcrumbData, dataList,loading } = this.state;
        return (
            <div className="systemLog">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* p-content-wrapper */}
                <div className='p-content-wrapper'>
                  {/*  */}
                  <div className="p-search">
                    <SearchForm handleSearchBtn={this.handleSearchBtn} />
                  </div>
                  <div className="table-list">
                    <Table 
                      loading={loading} 
                      data={dataList} 
                      handleChangePage={this.handleChangePage} 
                      handleDelete={this.handleDelete}
                    />
                  </div>
                  
                </div>
            </div>
        )
    }
}
export default systemLog