import React, { Component } from 'react'
import { Form, Input, Modal, Select, DatePicker } from 'antd'
import './index.less'
import Api from '../../../api/index';
const { MonthPicker, RangePicker, WeekPicker } = DatePicker
const { Option } = Select;
const { TextArea } = Input;
const App = Form.create({ name: 'form_in_modal' })(
    class extends Component {
        constructor(props) {
            super(props)
            this.state = {
                selectTime: [],
                listTypeDetailsData: [],//
                listExamForPlanData: [],//
            }
        }
        componentDidMount() {
            this.listTypeDetails()//
            this.listExamForPlan()//
        }
        onChange = (e, dateString) => {
            console.log(e, dateString,'555555555555');
            this.setState({
                selectTime: dateString
            })
        }    
        onChangeTwo(date, dateString) {
            console.log(date, dateString,'4444444444');
        }

        //
        listTypeDetails = () => {
            Api.listTypeDetails({
                itemsId: 4
            }, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listTypeDetailsData: res.data.list
                    })
                }
            })
        }
        //
        listExamForPlan = () => {
            Api.listExamForPlan({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listExamForPlanData: res.data
                    })
                }
            })
        }
        render() {
            const {
                visible, onCancel, onCreate, form, labelList, isEdit,
            } = this.props;
            const {
                listTypeDetailsData,
                listExamForPlanData
            } = this.state;
            const { getFieldDecorator } = form;
            const formItemLayout = {
                labelCol: { span: 6 },
                wrapperCol: { span: 18 },
            };

            //
            const listTypeDetails = listTypeDetailsData ? listTypeDetailsData.map(res => {
                return (<Option key={res.bizId} values={res.bizId}>{res.itemName}</Option>)
            }) : null
            //
            const listExamForPlan = listExamForPlanData ? listExamForPlanData.map(res => {
                return (<Option key={res.examId} values={res.examId}>{res.examName}</Option>)
            }) : null

            const _title = isEdit ? "--" : "--"
            return (
                <Modal
                    visible={visible}
                    title={_title}
                    okText=""
                    cancelText=''
                    onCancel={onCancel}
                    onOk={onCreate}
                    maskClosable={false}
                >
                    <Form>
                        <Form.Item
                            label={''}
                            {...formItemLayout}
                        >
                            {getFieldDecorator('bizId', {
                                // initialValue: isEdit ? 
                                rules: [{
                                    required: true, message: `!`,
                                }],
                            })(
                                <Select
                                    allowClear
                                    placeholder=""
                                    onChange={this.queryEleList}
                                    getPopupContainer={triggerNode => triggerNode.parentNode}
                                >
                                    {listTypeDetails}
                                </Select>
                            )}
                        </Form.Item>
                        <Form.Item
                            label={''}
                            {...formItemLayout}
                        >
                            {getFieldDecorator('trainName', {
                                rules: [{
                                    required: true, message: `!`,
                                }],
                            })(
                                <Input style={{ width: 340 }} placeholder="" onKeyDown={this.handleKey} />
                            )}
                        </Form.Item>
                        <Form.Item
                            label={''}
                            {...formItemLayout}
                        >
                            {getFieldDecorator('examId', {
                                rules: [{
                                    required: true, message: `!`,
                                }],
                            })(
                                <Select
                                    allowClear
                                    placeholder=""
                                    onChange={this.queryEleList}
                                    getPopupContainer={triggerNode => triggerNode.parentNode}
                                >
                                    {listExamForPlan}
                                </Select>
                            )}
                        </Form.Item>
                        <Form.Item
                            label={''}
                            {...formItemLayout}
                        >
                            {getFieldDecorator('passPoint', {
                                rules: [
                                    {
                                        required: true,
                                        message: `!`,
                                    },
                                    // {
                                    //     max: 100,
                                    //     message: "100"
                                    // }
                                ],
                            })(
                                <Input style={{ width: 340 }} placeholder="" onKeyDown={this.handleKey} />
                            )}
                        </Form.Item>
                        <Form.Item
                            label={''}
                            {...formItemLayout}
                        >
                            {getFieldDecorator('readFen', {
                                rules: [
                                    {
                                        required: true,
                                        message: `!`,
                                    },
                                    // {
                                    //     max: 200,
                                    //     message: "200"
                                    // }
                                ],
                            })(
                                <Input style={{ width: 340 }} placeholder="" onKeyDown={this.handleKey} />
                            )}
                        </Form.Item>
                        <Form.Item
                            label={''}
                            {...formItemLayout}
                        >
                            {getFieldDecorator('trainDescription', {
                                rules: [{
                                    required: true, message: `!`,
                                }],
                            })(
                                <TextArea style={{ width: 340, height: 100 }} placeholder="" />
                            )}
                        </Form.Item>
                        <Form.Item
                            label={''}
                            {...formItemLayout}
                        >
                            {getFieldDecorator('startDate', {
                                rules: [{
                                    required: true, message: `!`,
                                }],
                            })(
                                <DatePicker onChange={this.onChange} onClick={ () => this.props.GetStates(this.state.dataTime)} />
                            )}
                        </Form.Item>
                        <Form.Item
                            style={{ marginBottom: 0 }}
                            {...formItemLayout}
                            label=""
                        >
                            {getFieldDecorator('endDate', {
                                rules: [
                                ],
                            })(
                                <DatePicker onChange={this.onChangeTwo} />
                            )}
                        </Form.Item>
                    </Form>
                </Modal>
            );
        }
    }
);
export default App